import Vue from 'vue'
import VueRouter from 'vue-router'
import pathAll from '@/router/pathAll'
// import Login from '@/views/Login/Login'
const Login = () => import(/* webpackChunkName:"login_home_welcome" */ '@/views/Login/Login')
// import Home from '@/views/Home/home'
const Home = () => import(/* webpackChunkName:"login_home_welcome" */ '@/views/Home/home')
// import Welcome from '@/views/Welcome/Welcome'
const Welcome = () => import(/* webpackChunkName:"login_home_welcome" */ '@/views/Welcome/Welcome')
// import Users from '@/views/Home/Children/Users'
const Users = () => import(/* webpackChunkName:"Users_Rights_Roles" */ '@/views/Home/Children/Users')
// import Rights from '@/views/Home/Children/Rights'
const Rights = () => import(/* webpackChunkName:"Users_Rights_Roles" */ '@/views/Home/Children/Rights')
// import Roles from '@/views/Home/Children/Roles'
const Roles = () => import(/* webpackChunkName:"Users_Rights_Roles" */ '@/views/Home/Children/Roles')

// import Cate from '@/views/Home/Children/Goods/Cate'
const Cate = () => import(/* webpackChunkName:"Cate_Params" */ '@/views/Home/Children/Goods/Cate')
// import Params from '@/views/Home/Children/Goods/Params'
const Params = () => import(/* webpackChunkName:"Cate_Params" */ '@/views/Home/Children/Goods/Params')

// import List from '@/views/Home/Children/Goods/List'
const List = () => import(/* webpackChunkName:"List_Add_Edit" */ '@/views/Home/Children/Goods/List')
// import Add from '@/views/Home/Children/Goods/Add'
const Add = () => import(/* webpackChunkName:"List_Add_Edit" */ '@/views/Home/Children/Goods/Add')
// import Edit from '@/views/Home/Children/Goods/Edit'
const Edit = () => import(/* webpackChunkName:"List_Add_Edit" */ '@/views/Home/Children/Goods/Edit')

// import Order from '@/views/Home/Children/Order/Order'
const Order = () => import(/* webpackChunkName:"Order_Report" */ '@/views/Home/Children/Order/Order')
// import Report from '@/views/Home/Children/Report/Report'
const Report = () => import(/* webpackChunkName:"Order_Report" */ '@/views/Home/Children/Report/Report')

Vue.use(VueRouter)

const routes = [
  {
    path: '/', redirect: '/login'
  },
  {
    path: '/login', component: Login
  },
  {
    path: '/home',
    component: Home,
    redirect: '/welcome',
    children: [
      {
        path: '/welcome', component: Welcome
      },
      {
        path: '/users', component: Users
      },
      {
        path: '/rights', component: Rights
      },
      {
        path: '/roles', component: Roles
      },
      {
        path: '/categories', component: Cate
      },
      {
        path: '/params', component: Params
      },
      {
        path: '/goods',
        component: List,
        children: [
          {
            path: '/goods/edit', component: Edit
          }]
      },
      {
        path: '/goods/add', component: Add
      },
      {
        path: '/goods/edit:id', component: Edit, props: true
      },
      {
        path: '/orders', component: Order
      },
      {
        path: '/reports', component: Report
      }
    ]
  }
]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  if (pathAll.indexOf(to.path) !== -1) {
    const token = window.sessionStorage.getItem('token')
    if (token) {
      next()
    } else {
      next('/login')
    }
  } else {
    next()
  }
})

export default router
